<template>
  <tr class="product-list-item">
    <td><img :src="`../img/${img}`" alt="Prduct image" /></td>
    <td>{{ name }}</td>
    <td>{{ category }}</td>
    <td>{{ price }} MAD</td>
    <td>
      <i @click="$emit('edit',this.productIndex)" class="far fa-edit text-success" ></i>
      <i @click="$emit('delete',this.productIndex)" class="far fa-trash-alt text-danger mx-3"></i>
    </td>
  </tr>
</template>

<script>
export default {
  name: "ProductItem",
  props: {
    productIndex: Number,
    img: String,
    name: String,
    category: String,
    price: String,
  },
};
</script>

<style>
.product-list-item img{
  height: 60px;
}
.product-list-item td{
  vertical-align: middle !important;
}
.product-list-item td i{
  cursor: pointer;
}
</style>
