<template>
  <transition name="model">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-dialog">
          <div class="modal-content border-0">
            <div class="modal-header" :class="{ 'bg-danger' : danger }">
              <h4 class="modal-title" :class="{ 'text-white' : danger }">
                {{ title }}
              </h4>
              <button
                @click="$emit('close')"
                type="button"
                class="close"
                aria-hidden="true"
                :class="{ 'text-white' : danger }"
              > &times;
              </button>
            </div>
            <div class="modal-body">

              <slot name="body"/>
              <br />
              <div align="center">
                <slot name="footer"/>
              </div>

            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: "Modal",
  props: {
    title: String,
    danger: Boolean
  },
};
</script>

<style>
.close {
  color: red;
  background: none;
  border: none;
  font-size: 1.4rem;
}
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: table;
  transition: opacity 0.3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}
</style>
