<template>
  <div
    class="jumbotron jumbotron-fluid rounded bg-white border-0 shadow-sm border-left px-4"
  >
    <div class="container">
      <h2 align="center" class="my-4">Products</h2>
      <button @click="addProduct" class="btn btn-primary">Add Product</button>
      <div class="table-responsive">
        <table class="table table-hover mt-2 mb-4">
          <thead style="color: white; background-color: #00D2D2;">
            <tr>
              <th>Image</th>
              <th>Name</th>
              <th>Category</th>
              <th>Price</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            <ProductItem
              v-for="(product, index) in products"
              @delete="deleteProduct"
              @edit="editProduct"
              :key="index"
              :productIndex="index"
              :img="product.img"
              :name="product.name"
              :category="product.category"
              :price="product.price"
            />
          </tbody>
        </table>
      </div>
    </div>
  </div>
  <Modal v-if="showModal" @close="closeModal" :title="modalTitle">
    <template v-slot:body>
      <div class="form-group">
        <label>Product Name</label>
        <input type="text" class="form-control" v-model="name" />
      </div>
      <div class="form-group">
        <label>Product Category</label>
        <input type="text" class="form-control" v-model="category" />
      </div>
      <div class="form-group">
        <label>Product price</label>
        <input type="text" class="form-control" v-model="price" />
      </div>
      <div class="form-group">
        <label>Product Image</label>
        <input type="text" class="form-control" v-model="img" />
      </div>
    </template>
    <template v-slot:footer>
      <input
        type="button"
        class="btn btn-dark mx-1 btn-xs"
        value="Cancel"
        @click="closeModal"
      />
      <input
        type="button"
        class="btn btn-success btn-xs"
        :value="modalActionButton"
        @click="saveChanges"
      />
    </template>
  </Modal>

  <Modal v-if="showDeleteModal" @close="closeDeleteModal" :title="modalTitle" :danger="dangerModal">
    <template v-slot:body>
      <p align="center"> Do you really wanna remove this Product ?</p>
    </template>
    <template v-slot:footer>
      <input
        type="button"
        class="btn btn-dark mx-1 btn-xs"
        value="No"
        @click="closeDeleteModal"
      />
      <input
        type="button"
        class="btn btn-danger btn-xs"
        value="Yes"
        @click="acceptDelete"
      />
    </template>
  </Modal>

</template>

<script>
import axios from "axios";
import ProductItem from "../../components/ProductItem.vue";
import Modal from "../../components/Modal.vue";
export default {
  name: "Products",
  components: { ProductItem, Modal },
  created() {
    axios
      .get("products")
      .then((res) => (this.products = res.data))
      .catch((err) => console.log(err));
  },
  data() {
    return {
      products: [],
      name: "",
      img: "",
      price: "",
      category: "",
      productIndex: "",
      modalTitle: "",
      showModal: false,
      showDeleteModal: false,
      dangerModal : false
    };
  },
  methods: {
    closeModal() {
      this.showModal = false
    },
    closeDeleteModal() {
      this.showDeleteModal = false
    },
    addProduct() {
      this.name = ""
      this.category = ""
      this.price = ""
      this.img = ""
      this.modalActionButton = "Add"
      this.modalTitle = "Add Product"
      this.showModal = true
      this.dangerModal = false
    },
    editProduct(productIndex) {
      this.name = this.products[productIndex].name
      this.category = this.products[productIndex].category
      this.price = this.products[productIndex].price
      this.img = this.products[productIndex].img
      this.productIndex = productIndex
      this.modalActionButton = "Save"
      this.modalTitle = "Edit Product"
      this.showModal = true
      this.dangerModal = false
    },
    deleteProduct(productIndex) {
      this.productIndex = productIndex
      this.modalTitle = 'Remove Category'
      this.dangerModal = true
      this.showDeleteModal = true
    },
    acceptDelete() {
        axios
        .delete("products/" + this.products[this.productIndex].id)
        .then( _ => {
          this.products.splice(this.productIndex,1) 
          this.showDeleteModal = false
        })
        .catch( err => console.log(err) )
    },
    saveChanges() {
      if(this.modalActionButton === 'Save')
      {
        axios
            .put('products/' + this.products[this.productIndex].id,{
              name: this.name,
              category: this.category,
              price: this.price,
              img: this.img
            })
            .then( res => {
              this.products[this.productIndex] = res.data
              this.showModal = false
            })
            .catch( err => console.log(err) )
      } else{
            axios
            .post('products',{
              name: this.name,
              category: this.category,
              price: this.price,
              img: this.img
            })
            .then( (res) => {
              this.products.push(res.data)
              this.showModal = false
            })
            .catch( err => console.log(err) )
      }
    }
  },
}
</script>